*{
  box-sizing: border-box !important;
}
ul li{
  list-style: none;
}
a{
  text-decoration: none;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background:#000;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.section-wrapper{
  margin:auto;
  width:430px;
  position: relative;
  overflow: hidden;
  background-color:#fff;
  background-image: linear-gradient(158.932deg, #C07641 0%, #FFFFFF 75%);
  padding:24px;
  padding-top:99px;
  height:932px;
  text-align:left;
  border-radius: 24px;
  margin-bottom:24px;
}
.section-wrapper h2{
  font-family: 'raleway';
  font-weight: 300;
  font-size:56px;
  padding-top:50px;
  padding-left:14px;
}
.section-wrapper .bold-tag{
  font-weight: bold;
  color:#C07641 !important;
}
