@keyframes slow-tilt {
          0%   { transform: rotateZ(-15deg); }
          50%  { transform: rotateZ(-5deg); }
          100% { transform: rotateZ(-15deg); }
}
.cup6-model-wrapper img{
  width:145% !important;
  margin-left:-18%;
  transform: rotateZ(-10deg);
  animation: slow-tilt 15s infinite ease-in-out;
}
.cup7-model-wrapper{
  position:absolute;
  top: -9550px;
  right:-115px;
  width: 420px;
  height:940px;
}
.logo img{
  width:74px;
}
.logo{
  z-index:99;
  position:relative;
  text-align: left;
}

.section-content{
  width: 100%;
  border-radius: 24px;
  background: linear-gradient(159.877deg, rgba(192, 118, 65, 0.8) 0%, rgba(255,255,255,0.6) 40%);
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.08);
  /* border: 1px solid rgba(0, 0, 0, 0.11); */
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px); /* Safari desteği için */
  position: relative;
  z-index: 99;
  margin-top:100px;
}
.section-content::before {
  content: "";
  position: absolute;
  top: 0; left: 0; right: 0; bottom: 0;
  border: 1px solid rgba(0, 0, 0, 0.09);
  border-radius: 24px;
  pointer-events: none;
  z-index: 1;
}
#section-content-header{
  height: 385px;
}
#to-menu-wrpper{
  width:100%;
  position: absolute;
  bottom: 53px;
  display:flex;
  justify-content: center;
}
#to-menu{
  font-family: 'vazir';
  padding: 7px 29px;
  border: 1px solid #000;
  border-radius: 50px;
  font-size: 21px;
  font-weight: bold;
  font-weight: 300;
  cursor: pointer;
  background:black;
  color:white;
}
#to-menu svg{
    margin-bottom: -3px;
    display:inline;
    margin-right:9px;
}
ul#header-navbar {
  -webkit-overflow-scrolling: touch;
  width: 100%;
  text-align: right;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
  z-index: 999;
  padding: 0;
  justify-content: center;
  margin-top:71px
}

/* List item stilleri */
ul#header-navbar li {
  font-family: 'vazir';
  padding: 4px 29px;
  border: 3px solid #000;
  border-radius: 50px;
  font-size: 18px;
  font-weight: 600;
  margin: 0 0 13px 10px;
  backdrop-filter: blur(2px);
  background-color: rgba(255, 255, 255, 0.45);
  white-space: wrap;
  font-weight: bold;
}
ul#header-navbar a{
  color:black
}
.temp-content{
  height: 580px;
  padding:24px;
  padding-bottom:27px;
}
.titles{
  position:absolute;
  z-index: 9999;
  color:white;
  top:0;
  /* font-weight: bold !important; */
  padding:0 !important;
  font-size:52px !important;
  text-shadow: 0 0 6px rgba(73, 73, 73, 0.8);

}
.slider-buttons-wrapper {
  display: flex;
  justify-content: center;
  gap: 10px;
  position: absolute;
  bottom: 50px;
  right: 24px;
  direction: rtl;
  align-content: center;
  align-items: center;
  width: 100%;
  left: 0;
  right: 0;
}

.slider-button {
font-family: 'vazir';
  padding: 10px 29px;
  border: 3px solid #000;
  border-radius: 50px;
  font-size: 18px;
  font-weight: 600;
  backdrop-filter: blur(2px);
  background-color: rgba(255, 255, 255, 0.45);
  white-space: wrap;
  font-weight: bold;
}

.slider-button.active {
  background: #000;
  color: #fff;
}
.menu-section {
  display: flex;
  flex-direction: column;
  height: 100%;
  direction: rtl;
  font-family: 'Vazir', sans-serif;
  padding: 8px;
}

.menu-title {
  display: block;
  padding: 7px 17px;
  color: #fff;
  border-radius: 5px;
  width: fit-content;
  text-shadow: 0 0 6px rgba(73, 73, 73, 0.8);
  margin:0;
  font-size:22px;
  margin-bottom:25px;
}

.menu-scroll-area {
  flex: 1;
  overflow-y: auto;
  padding:0 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.menu-item {
  display: flex;
  justify-content: space-between;
  font-size: 19px;
  padding: 9px 0;
  border-bottom: 1px dashed #333 ;
  color: #3d3d3d;
}
.menu-item:first-child{
    padding: 15px 0;
    padding-top: 10px;
}
.simplebar-content .menu-item:last-child{
  border-bottom:none;
}